.MobileSettings{
    .wrapper{
        margin: 2vw 5vw 0;
        .topMobileSettings {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            h3 {
                color: #FFF;
                text-align: center;
                font-family: var(--font-family-urbanist);
                font-size: 16px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
            }
            span{
                color: #354DFF;
                text-align: center;
                font-family: var(--font-family-urbanist);
                font-size: 13px;
                font-style: normal;
                font-weight: 500;
                line-height: 150%; /* 19.5px */
                letter-spacing: 0.26px;
            }
        }   
    }
    
}


@media screen and (max-width: 480px) {
    .MobileSettings{
        .wrapper{
            margin: 0 1vw;
        }
    }
    
}