.PhotoSlider {
	&Avatar {
		position: relative;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		.dots {
			position: absolute;
			top: 20px;
			left: 50%;
			width: 92%;
			transform: translateX(-50%);
			display: flex;
			gap: 4px;
			z-index: 1;

			.dot {
				height: 2px;
				flex-grow: 1;
				flex-shrink: 1;
				border-radius: 10px;
				opacity: 0.2;
				background: #fff;

				&.active {
					opacity: 0.7;
				}
			}
		}

        .arrow {
            position: absolute;
            top: 0;
            bottom: 0;
            height: 100%;
            z-index: 1;
            width: 50%;
            cursor: pointer;

            &--left {
                left: 0;
            }

            &--right {
                right: 0;
            }
        }
	}
}
