.ToggleLikeTop{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    width: 90vw;
    margin: 0 auto;
    .item{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        padding-bottom: 15px;
        border-bottom: 2px solid transparent;
        transition: all ease 0.3s;
        cursor: pointer;
        span{
            color: #A2A2A2;
            font-family: var(--font-family-urbanist);
            font-size: clamp(9px, 4vw, 14px);
            transition: all ease 0.3s;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            white-space: nowrap;
        }
        svg{
            transition: all ease 0.3s;
        }
    }
    .active{
        border-bottom: 2px solid white;
        transition: all ease 0.3s;
        span{
            transition: all ease 0.3s;
            color: white;
        }
        svg{
            transition: all ease 0.3s;
            stroke: black;
            fill: white;
        }
    }
}
