.Subscription{
    .subscriptionWrapper {
      
    }
    .navigation-wrapper {
        width: 600px;
        overflow: hidden;
        .keen-slider {
            width: max-content;
            gap: 10px;
            left: 0.2vw;
            overflow: visible;
            // min-width: 540px;
            // max-width: 600px;

          .keen-slider__slide{
//             min-width: 100% !important;
//   max-width: 100% !important;
            
            // transform: unset !important;
            
            .banner{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 28px 15px;
                // max-width: 550px;
                .bannerImg{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    svg{
                        width: 110px;
                        height: 30px;
                    }
                    .marker{
                        border-radius: 5px;
                        background: var(--black);
                        margin: 0 10px;
                        span{
                            padding: 0 5px;
                            color: rgb(225, 162, 0);
                            font-family: var(--font-family-urbanist);
                            font-size: 12px;
                            font-weight: 600;
                            line-height: 14px;
                            letter-spacing: 2%;
                            text-align: center;
                        }
                    }
                }
                .buyBtn{
                    border-radius: 30px;
                    padding: 10px;
                    cursor: pointer;
                    background: linear-gradient(135.00deg, rgb(132, 9, 56) -0.14%,rgb(242, 34, 113) 99.86%),linear-gradient(135.00deg, rgb(24, 25, 32) 0%,rgb(15, 15, 19) 100%);
                    span{
                        color: var(--white);
                        font-family: var(--font-family-urbanist);
                        font-size: 10px;
                        font-weight: 700;
                        line-height: 12px;
                        letter-spacing: 0%;
                        text-align: center;
                    }
                }
            }
            .plus{
                border-radius: 10px;
                background: linear-gradient(180.00deg, rgb(154, 24, 255),rgb(191, 10, 255) 100%);
                .bannerImg{
                    .marker{
                        background: rgb(255, 187, 11);
                        span{
                            color: var(--black);
                        }
                    }
                }
            }

            .gold{
                border-radius: 10px;
                background: linear-gradient(180.00deg, rgb(255, 190, 24),rgb(255, 186, 10) 100%);
            }
            .vip{
                border-radius: 10px;
                background: linear-gradient(135.58deg, rgb(205, 205, 205) 0%,rgb(255, 255, 255) 98.339%);
                .bannerImg{
                    .marker{
                        background: var(--black);
                        span{
                            color: rgb(188, 188, 188);
                           
                        }
                    }
                }
            }

          }
        //   .number-slide1{


        //   }
        //   .number-slide2{
        //     transform: translate3d(-600px, 0px, 0px) !important;

        //   }
        //   .number-slide3{
        //     transform: translate3d(-1065px, 0px, 0px) !important;


        //   }

        }
      .dots{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
        gap: 10px;
        .dot{
            width: 6px;
            height: 6px;
            background: rgb(90, 90, 90);
            box-sizing: unset;
            border: none;
            border-radius: 50%;
            padding: 0;
            cursor: pointer;
            transition: all ease 0.3s;
        }
        .active{
            background: rgb(231, 3, 98);
            transition: all ease 0.3s;

        }
      }
    }
}