@media screen and (max-width: 1024px) {
    .GamePad {
        width: 100vw;
        // max-width: 600px;
        height: calc(100vh - clamp(102px, 13vw, 110px));
        padding: clamp(102px, 13vw, 110px) 0 0;
        margin: 0 auto;
        --m: 45px;

        &Block {
            width: 100%;
            height: 100%;
            position: relative;
        }

        &BlockItem {
            .PhotoSlider {
                height: 100%;
                width: 100%;
                border-radius: 10px;

                &Avatar {
                    height: 100%;
                    width: 100%;

                    .dots {
                        top: 10px;
                        width: 96%;
                    }
                }
            }

            img {
                border-radius: 10px;
            }
        }

        &PanelButtons {
            width: calc(100% - 30px);
        }

        &PanelBackground {
            padding-bottom: 90px;
            border-radius: 30px 30px 0 0;
        }
    }

    .App .pageWrapper {
        padding-bottom: 0 !important;
    }

    .profileOpened .App .pageWrapper {
        z-index: 10;

        .GamePad .ProfileComponent {
            position: fixed;
            z-index: 2;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}