.defaultBtn {
    background: none repeat scroll 0 0 transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: medium none;
    border-spacing: 0;
    font-weight: normal;
    line-height: 1.42rem;
    list-style: none outside none;
    margin: 0;
    padding: 16px 0;
    text-decoration: none;
    text-indent: 0;
    width: 100%;
    
    span{
        color: var(--white);
        font-family: Urbanist;
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        letter-spacing: 0%;
        text-align: center;
        padding: 0 10px;
    }

   
}