.videoChatModal{
    .modalWrapper{
    
        .avatarsBlock{
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                width: clamp(30px, 5vw, 60px);
                height: clamp(30px, 5vw, 60px);
                border-radius: 50%;

            }
            .owner{
                position: relative;
                right: 1vw;
            }
        }
        .tittle{
            margin: 10px 0;
            line-height: normal;
            h2{
                margin: 0;
            }
        }
        .description{
            max-width: 40%;
        }
        .description, .toggleToTalk {
            span{
                color: #A2A2A2;
                text-align: center;
                font-family: var(--font-family-urbanist);
                font-size: clamp(12px, 2vw, 14px);
                font-style: normal;
                font-weight: 500;
                line-height: 150%; /* 21px */
                letter-spacing: 0.28px;
            }
        }
        .toggleToTalk{
            padding: 20px 0;
            .main{
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                padding: 5px 0;
                .question {
                  max-width: 80%;
                  display: flex;
                  justify-content: flex-start;
                  flex-wrap: wrap;
                  span{
                    text-align: start;
                  }
                }

            }
            .sub{
                text-align: start;
                padding: 5px 0;


            }
        }
    }
}




@import url('./MediaChatModal.scss');